<template>
  <div class="register-container" v-show="!isRegister && showForm">
    <div class="banner">
      <img
        src="https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/register-banner.png"
        alt=""
      />
    </div>
    <van-form
      @submit="onSubmit"
      input-align="right"
      :show-error-message="true"
      :show-error="false"
      :disabled="formDiabled"
    >
      <van-cell-group inset style="margin:0">
        <van-field
          v-model="shopName"
          name="shopName"
          label=""
          placeholder=""
          maxlength="10"
          error-message-align="right"
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="contactName"
          name="contactName"
          label=""
          placeholder=""
          maxlength="10"
          error-message-align="right"
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="male"
          :is-link="!formDiabled"
          readonly
          name="male"
          label=""
          placeholder=""
          error-message-align="right"
          @click="handleClickField('showMalePicker')"
          :rules="[{ required: true, message: '' }]"
        />
        <van-popup v-model:show="showMalePicker" position="bottom">
          <van-picker
            :columns="maleColumns"
            :columns-field-names="maleCustomFieldName"
            :default-index="maleIndex"
            @confirm="onMaleConfirm"
            @cancel="showMalePicker = false"
          />
        </van-popup>
        <van-field
          v-model="date"
          :is-link="!formDiabled"
          readonly
          name="date"
          label=""
          placeholder=""
          error-message-align="right"
          @click="handleClickField('showDatetimePicker')"
          :rules="[{ required: true, message: '' }]"
        />
        <van-popup v-model:show="showDatetimePicker" position="bottom">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            title=""
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="onDatetimeConfirm"
            @cancel="showDatetimePicker = false"
          />
        </van-popup>

        <van-field
          v-if="contactPhone"
          v-model="contactPhone"
          name="contactPhone"
          label=""
          placeholder=""
          error-message-align="right"
          :rules="[
            {
              required: true,
              message: ''
            },
            {
              required: true,
              pattern: phonePattern,
              message: ''
            }
          ]"
        />
        <van-field
          v-model="fieldValue"
          :is-link="!formDiabled"
          readonly
          label=""
          placeholder=""
          error-message-align="right"
          @click="handleClickField('showRegionPopup')"
          :rules="[{ required: true, message: '' }]"
        />
        <van-popup v-model:show="showRegionPopup" square position="bottom">
          <van-cascader
            v-model="cascaderValue"
            title=""
            :options="options"
            @close="showRegionPopup = false"
            @change="onRegionChange"
            @finish="onRegionFinish"
            :field-names="fieldNames"
          />
        </van-popup>
        <van-field
          v-model="detailAddr"
          name="detailAddr"
          label=""
          input-align="left"
          maxlength="50"
          error-message-align="left"
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="GPSValue"
          :is-link="!formDiabled"
          disabled
          readonly
          label=""
          placeholder=""
          error-message-align="right"
          right-icon="https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/rs-location.png"
          @click="handleClickField('showForm')"
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          name="uploader"
          label=""
          error-message-align="right"
          :rules="[{ required: true, message: '' }]"
        >
          <template #input>
            <van-uploader
              :deletable="!formDiabled"
              v-model="shopImg"
              capture="camera"
              max-count="1"
              :before-read="asyncBeforeRead"
              :after-read="(file) => afterRead(file, 'shopImg')"
              upload-text=""
              :max-size="6000 * 1024"
              @oversize="onOversize"
            />
          </template>
        </van-field>
        <van-field
          name="uploader"
          label=""
          error-message-align="right"
          :rules="[{ required: true, message: '' }]"
        >
          <template #input>
            <van-uploader
              :deletable="!formDiabled"
              v-model="licenseImg"
              capture="camera"
              max-count="1"
              :before-read="asyncBeforeRead"
              :after-read="(file) => afterRead(file, 'licenseImg')"
              upload-text=""
              :max-size="6000 * 1024"
              @oversize="onOversize"
            />
          </template>
        </van-field>
        <van-field
          v-model="licenseNo"
          label-width="10.2em"
          name="licenseNo"
          label=""
          placeholder=""
          error-message-align="right"
          :rules="[
            {
              required: true,
              message: ''
            },
            {
              required: true,
              pattern: licenseNoPattern,
              message: ''
            }
          ]"
        />
        <van-field name="radio" label="">
          <template #input>
            <van-radio-group
              v-model="areaType"
              :disabled="formDiabled"
              direction="horizontal"
            >
              <van-radio :name="1"></van-radio>
              <van-radio :name="2"></van-radio>
            </van-radio-group>
          </template>
        </van-field>
        <van-field
          v-model="saleZone"
          :is-link="!formDiabled"
          readonly
          name="saleZone"
          label=""
          placeholder=""
          error-message-align="right"
          @click="handleClickField('showSaleZonePicker')"
          :rules="[{ required: true, message: '' }]"
        />
        <van-popup v-model:show="showSaleZonePicker" position="bottom">
          <van-picker
            :columns="saleZoneCodeColumns"
            :columns-field-names="saleZoneCodeCustomFieldName"
            :default-index="saleZoneIndex"
            @confirm="onSaleZoneConfirm"
            @cancel="showSaleZonePicker = false"
          />
        </van-popup>
        <van-field
          v-model="salesman"
          name="salesman"
          label=""
          placeholder=""
          maxlength="10"
        />
      </van-cell-group>
      <div style="margin: 16px;" v-if="!formDiabled">
        <van-button block type="primary" native-type="submit">
          
        </van-button>
      </div>
      <div style="margin: 16px;" v-else>
        <van-button square block type="primary">
          
        </van-button>
      </div>
    </van-form>
  </div>
  <!--  -->
  <div class="register-location-container" v-if="!isRegister && !showForm">
    <iframe
      id="mapPage"
      width="100%"
      height="100%"
      frameborder="0"
      :src="iframeUrl"
    >
    </iframe>
  </div>
  <!--  -->
  <div class="is-register-container" v-if="isRegister">
    <!--  -->
    <template v-if="registerStatus === 1">
      <div class="icon">
        <img
          src="https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/rs-ok.png"
          alt=""
        />
      </div>
      <div class="status"></div>
      <div class="text"></div>
      <div class="text"></div>
      <div class="btn" v-if="hasPhone">
        <router-link to="/seller/sellerFans">
          <van-button type="primary" style="width:8rem"></van-button>
        </router-link>
      </div>
      <div class="btn" v-if="!hasPhone">
        <van-button
          type="primary"
          style="width:8rem"
          @click="showBindPhone = true"
          ></van-button
        >
        <van-popup
          v-model:show="showBindPhone"
          style="background-color: #f6f6f6;"
        >
          <div class="form-bind">
            <div class="title"></div>
            <van-form
              @submit="onSubmitPhone"
              :show-error-message="false"
              :show-error="true"
              input-align="left"
              label-width="3rem"
            >
              <van-cell-group inset>
                <van-field
                  v-model="contactPhone"
                  name="contactPhone"
                  label=""
                  placeholder=""
                  :rules="[
                    {
                      required: true,
                      pattern: phonePattern,
                      message: ''
                    }
                  ]"
                />
                <van-field
                  v-model="phoneCode"
                  name="phoneCode"
                  label=""
                  placeholder=""
                  center
                  @update:model-value="handleCodeChange"
                  :rules="[
                    {
                      required: true,
                      pattern: phoneCodePattern,
                      message: ''
                    }
                  ]"
                >
                  <template #button>
                    <van-button
                      :disabled="btnDisabled"
                      size="small"
                      type="primary"
                      @click="handleGetCode"
                      >{{ phoneCodeText }}</van-button
                    >
                  </template>
                </van-field>
              </van-cell-group>
              <div class="submit-phone-tips">
                <van-checkbox
                  v-model="submitChecked"
                  shape="square"
                  icon-size="4.2vw"
                  style="justify-content: center;"
                  ><a
                    href="javascript:;"
                    style="color:#1989fa"
                    @click="handleClickPolicy"
                    ></a
                  ></van-checkbox
                >
              </div>
              <div style="margin: 0 16px 16px;">
                <van-button
                  block
                  type="primary"
                  size="small"
                  native-type="submit"
                  style="width: 7rem;margin: auto;"
                >
                  
                </van-button>
              </div>
            </van-form>
          </div>
        </van-popup>
      </div>
    </template>
    <template v-if="registerStatus === 2">
      <div class="icon">
        <img
          src="https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/rs-wait.png"
          alt=""
        />
      </div>
      <div class="status"></div>
      <div class="text"></div>
      <!-- <div class="btn">
        <van-button
          square
          plain
          type="primary"
          style="width:8rem;background-color:#f6f6f6"
          ></van-button
        >
      </div> -->
    </template>
    <!--  -->
    <template v-if="registerStatus === 3">
      <div class="icon">
        <img
          src="https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/rs-failed.png"
          alt=""
        />
      </div>
      <div class="status"></div>
      <div class="text"></div>
      <div class="text" style="margin:1rem">:{{ auditMsg }}</div>
      <div class="btn">
        <van-button
          square
          plain
          type="primary"
          style="width:8rem;background-color:#f6f6f6"
          @click="isRegister = false"
          ></van-button
        >
      </div>
    </template>
    <PolicyPage v-if="showPolicy" @policyClose="handleClosePolicy" />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, ref } from 'vue'
import {
  CascaderOption,
  CascaderFieldNames,
  UploaderFileListItem,
  Toast,
  PickerFieldNames,
  PickerObjectOption
} from 'vant'
import Compressor from 'compressorjs'
import axios, { http } from '@/http'
import { useStore } from 'vuex'
import { GlobalDataProps } from '@/store'
import { MapKey } from '@/plugins/Tmap'
import PolicyPage from '@/components/PolicyPage/index.vue'
export default defineComponent({
  name: 'SellerRegister',
  components: { PolicyPage },
  setup() {
    const store = useStore<GlobalDataProps>()
    const lat = computed(() => store.state.lat)
    const lng = computed(() => store.state.lng)
    const iframeUrl = ref(
      `https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=${MapKey}&referer=hbseller`
    )
    const showPolicy = ref(false)
    const submitChecked = ref(false)
    const showBindPhone = ref(false)
    const hasPhone = ref(false)
    const contactPhone = ref('')
    const phoneCode = ref('')
    const phoneCodeText = ref('')
    const formDiabled = ref(false)
    const showForm = ref(true)
    const isRegister = ref(true) // 
    const registerStatus = ref(0) //  1: , 2: , 3: 
    const shopName = ref('')
    const contactName = ref('')
    const detailAddr = ref('')
    const salesman = ref('')
    const licenseNo = ref('')
    const password = ref('')
    const male = ref('')
    const maleIndex = ref(0)
    const saleZoneIndex = ref(0)
    const saleZone = ref('')
    const date = ref('')
    const auditMsg = ref('')
    const GPSValue = ref('')
    const showMalePicker = ref(false)
    const showSaleZonePicker = ref(false)
    const currentDate = ref(new Date())
    const showDatetimePicker = ref(false)
    const areaType = ref(1)
    const shopImg = ref<UploaderFileListItem[]>([])
    const licenseImg = ref<UploaderFileListItem[]>([])
    const maleColumns = [
      { name: '', id: 1 },
      { name: '', id: 2 }
    ]
    const maleCustomFieldName: PickerFieldNames = {
      text: 'name'
    }
    interface SaleZoneOption {
      zoneName: string
      zoneCode: string
    }
    const saleZoneCodeColumns = ref<SaleZoneOption[]>([])
    const saleZoneCodeCustomFieldName: PickerFieldNames = {
      text: 'zoneName'
    }

    let gender: string
    const onMaleConfirm = (value: PickerObjectOption) => {
      male.value = value.name
      gender = value.id
      showMalePicker.value = false
      console.log(gender)
    }

    let saleZoneCode: string
    const onSaleZoneConfirm = (value: PickerObjectOption) => {
      saleZone.value = value.zoneName
      saleZoneCode = value.zoneCode
      showSaleZonePicker.value = false
      console.log(saleZoneCode)
    }
    const onDatetimeConfirm = (value: Date) => {
      const d = new Date(value)
      date.value =
        d.getFullYear() +
        '-' +
        (d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1) +
        '-' +
        (d.getDate() - 0 < 10 ? '0' + d.getDate() : d.getDate())
      showDatetimePicker.value = false
      console.log(value)
    }

    const phonePattern = /^1[3456789]\d{9}$/
    const licenseNoPattern = /^\d{12}$|^\d{13}$/
    const phoneCodePattern = /^\d{6}$/

    const showRegionPopup = ref(false)

    const fieldValue = ref('') // 

    const cascaderValue = ref('') //  
    const fieldNames: CascaderFieldNames = {
      text: 'name',
      value: 'code',
      children: 'items'
    }
    const options = ref<CascaderOption>([])
    let option: CascaderOption
    let provName: string
    let cityName: string
    let countyName: string
    let provCode: string
    let cityCode: string
    let countyCode: string
    const onRegionChange = async ({ value, tabIndex }: CascaderOption) => {
      if (tabIndex === 0) {
        option = (options.value as CascaderOption[]).find(
          (i) => i.code === value
        ) as CascaderOption
        http
          .get('/syx/region/query/level', { parentCode: value })
          .then((res) => {
            if (res.code === '200') {
              res.data.map((i: { items: never[] }) => {
                i.items = []
              })
              option.items = res.data
            } else {
              Toast.fail(res.msg)
            }
          })
          .catch((err) => {
            console.log(err)
          })
      }
      if (tabIndex === 1) {
        const op = (option.items as CascaderOption[]).find(
          (i) => i.code === value
        ) as CascaderOption
        http
          .get('/syx/region/query/level', { parentCode: value })
          .then((res) => {
            if (res.code === '200') {
              op.items = res.data
            } else {
              Toast.fail(res.msg)
            }
          })
          .catch((err) => {
            console.log(err)
          })
      }
    }
    const onRegionFinish = ({ selectedOptions }: CascaderOption) => {
      console.log(selectedOptions)
      showRegionPopup.value = false
      fieldValue.value = selectedOptions
        .map((option: { name: string; code: string }, index: number) => {
          if (index == 0) {
            provName = option.name
            provCode = option.code
          }
          if (index == 1) {
            cityName = option.name
            cityCode = option.code
          }
          if (index == 2) {
            countyName = option.name
            countyCode = option.code
          }
          return option.name
        })
        .join('/')
    }

    const onOversize = (file: UploaderFileListItem) => {
      console.log(file)
      Toast(' 6Mb')
    }

    let shopImgCode: string
    let licenseImgCode: string
    const asyncBeforeRead = (file: File) =>
      new Promise((resolve, reject) => {
        if (
          file.type !== 'image/jpeg' &&
          file.type !== 'image/jpg' &&
          file.type !== 'image/png' &&
          file.type !== 'image/bmp'
        ) {
          Toast(' png/jpg/jpeg/bmp ')
          reject()
        } else {
          new Compressor(file, {
            quality: 0.6,
            maxWidth: 1024,
            success: resolve,
            error(err) {
              console.log(err.message)
            }
          })
        }
      })
    const afterRead = async (
      file: UploaderFileListItem,
      target: 'shopImg' | 'licenseImg'
    ) => {
      console.log(target, file.file)
      file.status = 'uploading'
      file.message = '...'

      const formData = new FormData()
      const imgFile = new window.File(
        [file.file as BlobPart],
        file.file?.name as string,
        {
          type: file.file?.type
        }
      )
      formData.append('file', imgFile)
      formData.append('channel', 'hebei-sellerInfo')
      console.log(formData, imgFile)
      axios
        .post('/hbact/ossCommon/uploadOne', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(({ data: res }) => {
          console.log(res)
          if (res.code === '200') {
            file.status = 'done'
            if (target === 'shopImg') {
              shopImgCode = res.data.rdmCode
              shopImg.value = [
                {
                  url: res.data.filePath
                }
              ]
            }
            if (target === 'licenseImg') {
              licenseImgCode = res.data.rdmCode
              licenseImg.value = [
                {
                  url: res.data.filePath
                }
              ]
            }
          }
          console.log(shopImgCode, licenseImgCode)
        })
        .catch((err) => {
          console.log(err)
          file.status = 'failed'
          file.message = ''
        })
    }

    const minDate = ref(new Date(1920, 0, 1))
    const maxDate = ref(new Date())

    let shopLat: number
    let shopLng: number

    type Flag =
      | 'showSaleZonePicker'
      | 'showForm'
      | 'showRegionPopup'
      | 'showDatetimePicker'
      | 'showMalePicker'
    const handleClickField = (flag: Flag) => {
      if (formDiabled.value) return
      if (flag === 'showSaleZonePicker') {
        showSaleZonePicker.value = true
      }
      if (flag === 'showForm') {
        if (lat.value && lng.value) {
          iframeUrl.value = `https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=${MapKey}&referer=webtest&coord=${lat.value},${lng.value}`
        }
        showForm.value = false
      }
      if (flag === 'showRegionPopup') {
        showRegionPopup.value = true
      }
      if (flag === 'showDatetimePicker') {
        showDatetimePicker.value = true
      }
      if (flag === 'showMalePicker') {
        showMalePicker.value = true
      }
    }

    const getSellerInfo = () => {
      http
        .get('/hbSeller/seller/isRegister', {})
        .then((res) => {
          if (res.code === '200') {
            console.log(res.data)
            hasPhone.value = res.data.hasPhone
            isRegister.value = res.data.isRegister
            if (res.data.isRegister) {
              registerStatus.value = res.data.registerStatus
              if (registerStatus.value === 3) {
                const regiserInfo = res.data.regiserInfo
                shopName.value = regiserInfo.shopName
                contactName.value = regiserInfo.contactName
                gender = regiserInfo.gender
                // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                const maleColumn = maleColumns.find((item, index) => {
                  if (item.id == regiserInfo.gender) {
                    maleIndex.value = index
                    return true
                  }
                })!
                male.value = maleColumn.name
                date.value = regiserInfo.birthday
                const birthday = (regiserInfo.birthday as string).split('-')
                const year = Number(birthday[0])
                const month = Number(birthday[1]) - 1
                const day = Number(birthday[2])
                currentDate.value = new Date(year, month, day)
                provName = regiserInfo.provName
                cityName = regiserInfo.cityName
                countyName = regiserInfo.countyName
                fieldValue.value = provName + '/' + cityName + '/' + countyName
                provCode = regiserInfo.provCode
                cityCode = regiserInfo.cityCode
                countyCode = regiserInfo.countyCode
                detailAddr.value = regiserInfo.detailAddr
                if (regiserInfo.shopLat && regiserInfo.shopLng) {
                  GPSValue.value = ''
                  shopLat = regiserInfo.shopLat
                  shopLng = regiserInfo.shopLng
                }
                licenseImgCode = regiserInfo.licenseImgCode
                licenseImg.value = [
                  {
                    url: regiserInfo.licenseImg as string
                  }
                ]
                shopImgCode = regiserInfo.shopImgCode
                shopImg.value = [
                  {
                    url: regiserInfo.shopImg as string
                  }
                ]
                licenseNo.value = regiserInfo.licenseNo
                areaType.value = regiserInfo.areaType
                saleZoneCode = regiserInfo.saleZoneCode
                // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                const saleZoneColumn = saleZoneCodeColumns.value.find(
                  (item, index) => {
                    if (item.zoneCode == regiserInfo.saleZoneCode) {
                      saleZoneIndex.value = index
                      return true
                    }
                  }
                )!
                saleZone.value = saleZoneColumn.zoneName
                salesman.value = regiserInfo.salesman || ''
                auditMsg.value = regiserInfo.auditMsg || ''
                if (regiserInfo.contactPhone != null) {
                  contactPhone.value = regiserInfo.contactPhone
                }
              }
            }
          } else {
            Toast.fail(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }

    const onSubmit = () => {
      const params = {
        licenseNo: licenseNo.value,
        licenseImgCode,
        shopName: shopName.value,
        shopImgCode,
        contactName: contactName.value,
        areaType: areaType.value,
        provCode,
        provName,
        cityCode,
        cityName,
        countyCode,
        countyName,
        detailAddr: detailAddr.value,
        saleZoneCode,
        shopLat,
        shopLng,
        salesman: salesman.value,
        gender,
        birthday: date.value
      }
      console.log('submit', params)
      http
        .post('/hbSeller/seller/register', params, true)
        .then((res) => {
          if (res.code === '200') {
            Toast.success(res.msg)
            getSellerInfo()
          } else {
            Toast.fail(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }

    const onSubmitPhone = () => {
      if (!submitChecked.value) {
        Toast.fail('""')
        return
      }
      const params = {
        contactPhone: contactPhone.value,
        phoneCode: phoneCode.value
      }
      console.log(params)
      http
        .post('/hbSeller/seller/phoneCodeCheck', params, false)
        .then((res) => {
          if (res.code === '200') {
            Toast.success('')
            showBindPhone.value = false
            getSellerInfo()
          } else {
            Toast.fail(res.msg)
          }
        })
    }

    const codeCount = ref(60)
    const btnDisabled = ref(false)
    const handleCodeChange = () => {
      if (phoneCode.value.length > 6) {
        phoneCode.value = phoneCode.value.substring(0, 6)
      }
    }
    const handleGetCode = () => {
      if (!phonePattern.test(contactPhone.value)) {
        Toast.fail('')
        return
      }
      if (!submitChecked.value) {
        Toast.fail('')
        return
      }
      http
        .post(
          ' /hbSeller/seller/phoneCodeSend',
          { contactPhone: contactPhone.value },
          false
        )
        .then((res) => {
          if (res.code === '200') {
            Toast.success('')
            btnDisabled.value = true
            phoneCodeText.value = `(${codeCount.value--})`
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            let countInter: any = setInterval(() => {
              phoneCodeText.value = `(${codeCount.value--})`
              if (codeCount.value < 0) {
                clearInterval(countInter)
                countInter = undefined
                codeCount.value = 60
                phoneCodeText.value = ''
                btnDisabled.value = false
              }
            }, 1000)
          } else {
            Toast.fail(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }

    const getRegion = () => {
      http
        .get('/syx/region/query/level', { level: 1 })
        .then((res) => {
          if (res.code === '200') {
            res.data.map((i: { items: never[] }) => {
              i.items = []
            })
            options.value = res.data
          } else {
            Toast.fail(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }

    const getSaleZoneList = () => {
      http
        .get('/hbSeller/saleZone/allList', {})
        .then((res) => {
          if (res) {
            console.log(res)
            saleZoneCodeColumns.value = res
            getSellerInfo()
          } else {
            Toast.fail(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }

    const handleClickPolicy = () => {
      showPolicy.value = true
    }

    const handleClosePolicy = () => {
      submitChecked.value = true
      showPolicy.value = false
    }

    onMounted(async () => {
      if (!sessionStorage.getItem('SellerRegister')) {
        sessionStorage.setItem('SellerRegister', '1')
        window.location.reload()
        return
      }
      if (process.env.NODE_ENV !== 'production') {
        store.commit('setLng', 116.70602)
        store.commit('setLat', 39.860464)
      }
      getRegion()
      getSaleZoneList()
      window.addEventListener(
        'message',
        function(event) {
          // 
          const loc = event.data
          if (loc && loc.module == 'locationPicker') {
            //postmodule'locationPicker'
            console.log('location', loc)
            GPSValue.value = ''
            shopLat = loc.latlng.lat
            shopLng = loc.latlng.lng
            showForm.value = true
          }
        },
        false
      )
    })
    return {
      iframeUrl,
      formDiabled,
      isRegister,
      registerStatus,
      showForm,
      shopName,
      hasPhone,
      contactName,
      licenseNo,
      contactPhone,
      phoneCode,
      detailAddr,
      salesman,
      password,
      maleColumns,
      maleCustomFieldName,
      saleZoneCodeColumns,
      saleZoneCodeCustomFieldName,
      showMalePicker,
      showSaleZonePicker,
      currentDate,
      showDatetimePicker,
      areaType,
      onMaleConfirm,
      onSaleZoneConfirm,
      onDatetimeConfirm,
      male,
      maleIndex,
      saleZoneIndex,
      saleZone,
      GPSValue,
      date,
      shopImg,
      licenseImg,
      phonePattern,
      licenseNoPattern,
      phoneCodePattern,
      handleClickField,
      onSubmit,
      onSubmitPhone,
      minDate,
      maxDate,
      showRegionPopup,
      options,
      onRegionChange,
      onRegionFinish,
      fieldValue,
      fieldNames,
      cascaderValue,
      asyncBeforeRead,
      afterRead,
      onOversize,
      showBindPhone,
      phoneCodeText,
      handleGetCode,
      btnDisabled,
      handleCodeChange,
      auditMsg,
      submitChecked,
      showPolicy,
      handleClickPolicy,
      handleClosePolicy
    }
  }
})
</script>

