import { http } from '@/http';
import Dialog from "vant/es/dialog";
import "vant/es/dialog/style/less";
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
export const Draw = (params) => {
    return new Promise((resolve) => {
        http.post('/hbSeller/act/hbsDraw', params, false).then((res) => {
            if (res.code === '200') {
                resolve(res.data);
            }
            else {
                Dialog.alert({
                    title: '',
                    message: res.msg
                }).then(() => {
                    // on close
                });
            }
        });
    });
};
const getGift = (params, cb) => {
    if (params.awdType == 1) {
        window.location.href = '/yx/views/general/order-preview.html';
    }
    else {
        http
            .post('/syx/awd/rcv/virtual', {
            uaId: params.uaId
        }, false)
            .then((res) => {
            //code 701
            if (res.code === '701') {
                return;
            }
            //code700
            if (res.code === '700') {
                Dialog.alert({
                    title: '',
                    message: '---'
                })
                    .then(() => {
                    // on confirm
                    window.location.href = '/scoremall/html/member.html';
                })
                    .catch(() => {
                    // on cancel
                    cb();
                });
                return;
            }
            const tipsObj = {
                '200': '',
                '4001': '"10"0->~<br />24',
                '4002': '<br />->~<br />24',
                '4003': '<br />->~<br />24'
            };
            let msg = '';
            switch (res.code) {
                case '200':
                    msg = tipsObj[res.code];
                    Toast.success({
                        message: msg,
                        onClose: () => {
                            cb(res.code);
                        }
                    });
                    break;
                case '4001':
                    msg = tipsObj[res.code];
                    Dialog.alert({
                        title: '',
                        message: msg
                    }).then(() => {
                        // on close
                        cb();
                    });
                    break;
                case '4002':
                    msg = tipsObj[res.code];
                    Dialog.alert({
                        title: '',
                        message: msg
                    }).then(() => {
                        // on close
                        cb();
                    });
                    break;
                case '4003':
                    msg = tipsObj[res.code];
                    Dialog.alert({
                        title: '',
                        message: msg
                    }).then(() => {
                        // on close
                        cb();
                    });
                    break;
                default:
                    msg = res.msg;
                    Dialog.alert({
                        title: '',
                        message: msg
                    }).then(() => {
                        // on close
                        cb();
                    });
                    break;
            }
        });
    }
};
export const handleReceive = (params, cb) => {
    if (params == null)
        return;
    if (params.awdStatus === 0) {
        if (params.awdType != 1) {
            getGift(params, cb);
        }
        else {
            // 
            window.location.href = `/yx/views/general/order-preview.html?uaId=${params.uaId}&hbSeller=1`;
        }
        return;
    }
    if (params.awdStatus === 1 || params.awdStatus === 0) {
        if (params.awdType == 2 && params.awdJumpurl) {
            window.location.href = params.awdJumpurl;
            return;
        }
        window.location.href =
            '/yx/views/general/gift-detail.html?uaId=' + params.uaId;
        return;
    }
};
export const getActRules = (actCode) => {
    return new Promise((resolve) => {
        http.post('/hbSeller/act/ruleDesc', { actCode }, false).then((res) => {
            if (res.code === '200') {
                resolve(res.data.actRuleDesc || '');
            }
            else {
                Dialog.alert({
                    title: '',
                    message: res.msg
                }).then(() => {
                    // on close
                });
            }
        });
    });
};
