import { defineComponent } from 'vue';
export default defineComponent({
    name: 'ActTipsPopup',
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    setup(prop, ctx) {
        const handleClickClose = () => {
            ctx.emit('close');
        };
        return { handleClickClose };
    }
});
