import { defineComponent, onMounted, ref } from 'vue';
import { http } from '@/http';
import Dialog from "vant/es/dialog";
import "vant/es/dialog/style/less";
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { Draw, handleReceive, getActRules } from '@/plugins/hbsDraw';
import ActTipsPopup from '@/components/ActTipsPopup/index.vue';
import AwardPopup from '@/components/AwardPopup/index.vue';
import { handleClickJumpBtn } from '@/hooks/useJumpBtn';
import { useRouter } from 'vue-router';
export default defineComponent({
    name: 'Sign',
    components: { ActTipsPopup, AwardPopup },
    props: {
        actCode: {
            type: String,
            default: ''
        }
    },
    setup(props) {
        const ljSignDay = ref(0);
        const contDay = ref(0);
        const drawContDays = ref(0);
        const canDrawNum = ref(0);
        const ljSignStatus = ref(0); // 1:  2:  4:
        const showTips = ref(false);
        const showDrawNotes = ref(false);
        const showAwardPopup = ref(false);
        const showNoAwardPopup = ref(false);
        const showSignPopup = ref(false);
        const showGetChancePopup = ref(false);
        const actTips = ref('');
        const canDraw = ref(false);
        const isMaxContDay = ref(false);
        const contSignFlag = ref(false);
        const drawFlag = ref(false);
        const isSignToday = ref(false);
        const ljSignDrawChance = ref(false);
        const nextSignInfo = ref({
            contSignDays: 0,
            points: 0 //
        });
        const router = useRouter();
        const handleClickGiftsBtn = () => {
            handleClickJumpBtn(router, '/common/myGifts');
        };
        const drawData = ref(null);
        const getActInfo = () => {
            return new Promise((resolve) => {
                http.post(' /hbSeller/fansSign/actInfo', {}, false).then((res) => {
                    let signRecords = [];
                    if (res.code === '200') {
                        drawFlag.value = res.data.actConf.drawFlag;
                        contSignFlag.value = res.data.actConf.contSignFlag;
                        isSignToday.value = res.data.isSignToday;
                        ljSignStatus.value = res.data.ljSignStatus;
                        if (drawFlag.value) {
                            canDraw.value = res.data.canDraw;
                            canDrawNum.value = res.data.canDrawNum;
                            drawContDays.value = res.data.actConf.drawContDays || 0;
                            ljSignDay.value = res.data.ljSignDay || 0;
                        }
                        if (contSignFlag.value) {
                            contDay.value = res.data.contDay || 0;
                            isMaxContDay.value = res.data.isMaxContDay;
                            nextSignInfo.value.contSignDays =
                                res.data.nextSignInfo.contSignDays;
                            nextSignInfo.value.points = res.data.nextSignInfo.points;
                        }
                        signRecords = res.data.signRecords || [];
                    }
                    else {
                        Toast.fail(res.msg);
                    }
                    resolve(signRecords);
                });
            });
        };
        const signPointsList = ref([]);
        const drawHis = ref([]);
        const handleClickSignNotes = () => {
            http.post('/hbSeller/fansSign/drawHis', {}, false).then((res) => {
                if (res.code === '200') {
                    drawHis.value = res.data || [];
                    showDrawNotes.value = true;
                }
                else {
                    Dialog.alert({
                        title: '',
                        message: res.msg
                    }).then(() => {
                        // on close
                    });
                }
            });
        };
        const getDrawTicket = () => {
            showGetChancePopup.value = false;
            http.post('/hbSeller/fansSign/drawTicket', {}, false).then((res) => {
                if (res.code === '200') {
                    const { actCode, ticket } = res.data;
                    Draw({ actCode, ticket }).then((award) => {
                        console.log(award);
                        drawData.value = award;
                        if (drawData.value) {
                            showAwardPopup.value = true;
                        }
                        else {
                            showNoAwardPopup.value = true;
                        }
                    });
                }
                else {
                    Dialog.alert({
                        title: '',
                        message: res.msg
                    }).then(() => {
                        // on close
                    });
                }
            });
        };
        const handleColseTips = () => {
            showTips.value = false;
        };
        const handleCloseAwardPopup = () => {
            showAwardPopup.value = false;
        };
        const handleCloseSignPopup = () => {
            showSignPopup.value = false;
            if (ljSignDrawChance.value) {
                showGetChancePopup.value = true;
                ljSignDrawChance.value = false;
            }
        };
        const handleCloseNoAwardPopup = () => {
            showNoAwardPopup.value = false;
        };
        const handleCloseGetChancePopup = () => {
            showGetChancePopup.value = false;
        };
        const handleGetSignedAward = () => {
            Toast.success('!');
            handleCloseSignPopup();
        };
        const con = document.getElementsByClassName('con');
        const date = new Date();
        const showYear = ref(date.getFullYear()); //
        const showMonth = ref(date.getMonth() + 1); //
        const setCalender = (year, month, signRecords) => {
            con[0].innerHTML = '';
            const records = signRecords.map((item) => {
                return Number(item.day.slice(6)) + '';
            });
            const date = new Date();
            const nowyear = date.getFullYear(); //12
            const nowmonth = date.getMonth();
            const nowdate = date.getDate();
            // date.setDate(1) //
            date.setFullYear(year, month, 1);
            const dayCount = new Date(year, month + 1, 0).getDate(); //
            const Firstweek = date.getDay() == 0 ? 7 : date.getDay(); // 
            const list = [];
            let yindex = 0; // 
            let index = 1; //
            while (index <= dayCount) {
                list[yindex] = [];
                if (yindex == 0) {
                    //
                    for (let i = 0; i < 7; i++) {
                        if (i + 1 < Firstweek) {
                            //1
                            list[yindex][i] = '';
                        }
                        else {
                            list[yindex][i] = index;
                            index++;
                        }
                    }
                }
                else {
                    //
                    for (let i = 0; i < 7 && index <= dayCount; i++) {
                        list[yindex][i] = index;
                        index++;
                    }
                }
                yindex++;
            }
            for (const i in list) {
                let td = '';
                const tr = document.createElement('tr');
                for (const j in list[i]) {
                    if (year == nowyear && month == nowmonth && list[i][j] == nowdate) {
                        if (records.includes(list[i][j] + '')) {
                            list[i][j] = `<td class="allday nowday"><div class="signed">${list[i][j]}</div></td>`;
                        }
                        else {
                            list[i][j] = `<td class="allday nowday">${list[i][j]}</td>`;
                        }
                    }
                    else if (year == nowyear &&
                        month == nowmonth &&
                        records.includes(list[i][j] + '')) {
                        list[i][j] = `<td class="allday"><div class="signed">${list[i][j]}</div></td>`;
                    }
                    else {
                        list[i][j] = `<td class="allday ">${list[i][j]}</td>`;
                    }
                    td += list[i][j];
                }
                tr.innerHTML = td;
                con[0].appendChild(tr);
            }
        };
        //
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        // const left = () => {
        //   if (showMonth.value > 1) {
        //     showMonth.value--
        //     con[0].innerHTML = ''
        //     setCalender(showYear.value, showMonth.value - 1)
        //   } else {
        //     showMonth.value = 12
        //     showYear.value--
        //     con[0].innerHTML = ''
        //     setCalender(showYear.value, showMonth.value - 1)
        //   }
        // }
        //
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        // const right = () => {
        //   if (showMonth.value < 12) {
        //     showMonth.value++
        //     con[0].innerHTML = ''
        //     setCalender(showYear.value, showMonth.value - 1)
        //   } else {
        //     showMonth.value = 1
        //     showYear.value++
        //     con[0].innerHTML = ''
        //     setCalender(showYear.value, 0)
        //   }
        // }
        const initAct = () => {
            getActInfo().then((signRecords) => {
                setCalender(date.getFullYear(), date.getMonth(), signRecords);
            });
        };
        const handleClickSign = () => {
            http.post('/hbSeller/fansSign/doSign', {}, false).then((res) => {
                if (res.code === '200') {
                    signPointsList.value = res.data.pointsList || [];
                    if (res.data.ljSignDrawChance)
                        ljSignDrawChance.value = true;
                    if (signPointsList.value.length) {
                        showSignPopup.value = true;
                    }
                    else {
                        if (res.data.ljSignDrawChance) {
                            showGetChancePopup.value = true;
                        }
                        else {
                            Toast.success('!');
                        }
                    }
                    initAct();
                }
                else {
                    Dialog.alert({
                        title: '',
                        message: res.msg
                    }).then(() => {
                        // on close
                    });
                }
            });
        };
        const nextStep = () => {
            handleCloseAwardPopup();
            handleCloseNoAwardPopup();
            handleCloseGetChancePopup();
            initAct();
        };
        getActRules(props.actCode).then((rules) => {
            actTips.value = rules;
        });
        onMounted(() => {
            initAct();
        });
        return {
            contSignFlag,
            actTips,
            showTips,
            showDrawNotes,
            showAwardPopup,
            showNoAwardPopup,
            showGetChancePopup,
            showSignPopup,
            ljSignDay,
            contDay,
            drawContDays,
            showYear,
            showMonth,
            nextSignInfo,
            canDraw,
            isMaxContDay,
            isSignToday,
            drawFlag,
            signPointsList,
            drawHis,
            drawData,
            canDrawNum,
            ljSignStatus,
            getDrawTicket,
            handleClickSign,
            handleColseTips,
            handleCloseAwardPopup,
            handleCloseNoAwardPopup,
            handleCloseSignPopup,
            handleClickSignNotes,
            handleGetSignedAward,
            nextStep,
            handleReceive,
            handleClickGiftsBtn
        };
    }
});
